.header-inner {
	position: relative;
	padding-top: 215px;
	padding-bottom: 20px;
	background: url("../img/inner-page-bg.jpg") center no-repeat;
	background-size: cover;
	@include r(992) {
		padding: 150px 0 70px; }
	&__title {
		@include r(992) {
			text-align: center; }
		h1 {
			font-family: $title-font;
			font-weight: 700;
			font-size: 42px;
			color: $golden-tainoi;
			line-height: 1em;
			@include r(768) {
				font-size: 36px; }
			@include r(576) {
				font-size: 32px; } }
		h2 {
			font-weight: 500;
			font-size: 20px;
			color: $white;
			@include r(992) {
				font-size: 18px; }
			@include r(768) {
				font-size: 16px; }
			@include r(576) {
				font-size: 14px; } } } }
